<template>
  <div class="project">
    <h1 class="blind">project page</h1>
    <ul class="project-list">
      <!-- 프로젝트 리스트 -->
      <li v-for="(item, index) in items">
        <div>
          <strong><a :href="`${item.url}`" target="_blank">{{ item.title }}</a></strong>
          <div class="date">{{ item.date }}</div>
          <img :src="`${publicPath}${item.img}`" alt="">
        </div>
      </li>
    </ul>
    <p>
    </p>
  </div>
</template>

<script>
export default {
  data () {
    return {
      publicPath: process.env.BASE_URL,
      elementPos: [],
      items: [
        {
          url : 'https://tv.zum.com/',
          title : 'TV줌',
          date: '2018',
          img: 'images/service_bg_tvzum.jpg'
        },
        {
          url : 'http://hub.zum.com/',
          title : '허브줌',
          date: '2016~',
          img: 'images/service_bg_hubzum.jpg'
        },
        {
          url : 'http://valley.egloos.com/m',
          title : '이글루스',
          date: '2017~',
          img: 'images/service_bg_egloos.jpg'
        },
        {
          url : '',
          title : '타임트리',
          date: '2015~2017',
          img: 'images/service_bg_timetree.jpg'
        },
      ]
    };
  },
  methods: {
    handleScroll (event) {
      // 스크롤 시 실행할 이벤트 작성
      const element = document.querySelectorAll(".project-list li");
      let scollPos = window.scrollY + 50;
      if ( scollPos > this.elementPos[1] ) {
        element[1].classList.add('on')
      }
      if ( scollPos > this.elementPos[2]) {
        element[2].classList.add('on')
      }
      if ( scollPos > this.elementPos[3]) {
        element[3].classList.add('on')
      }
    }
  },
  created () {
    window.addEventListener('scroll', this.handleScroll);
  },
  destroyed() {
    window.removeEventListener('scroll', this.handleScroll);
  },
  mounted() {
    window.addEventListener('scroll', this.handleScroll)
    this.handleScroll;
    document.querySelectorAll(".project-list li").forEach(item => {
      this.elementPos.push(item.offsetTop)
    });
  }
}
</script>

<style scoped lang="scss">
.project-list {
  > li {
    opacity: 0;
    -webkit-transition: opacity .5s ease-in;
    transition: opacity .5s ease-in;
    &.on, &:first-of-type {
      opacity: 1;
    }
    padding-bottom:80px;
    color: rgb(85, 85, 85);
    font-size: 12px;
    strong {
      font-size: 20px;
      font-weight: bold;
      line-height: 70px;
    }
  }
}
@media (max-width: 768px) {
  .project-list li img {width:100%}
}
</style>